function assertNegUnlimited(actualValue) {
    return {
        pass: actualValue === Number.NEGATIVE_INFINITY,
        message: 'Expected actualValue not to be -Infinity.  actualValue is,' + actualValue
    };
}

export default assertNegUnlimited;
